"use client";

import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface DueThisWeek {
  noPinjaman: string;
  namaAnggota: string;
  jenis: string;
  jumlah: string;
  tanggal: string;
  status: string;
}

const getStatusStyle = (status: string) => {
  switch (status) {
    case "Hari Ini":
      return "bg-red-100 text-red-700";
    case "Besok":
      return "bg-yellow-100 text-yellow-700";
    case "2 Hari":
    case "3 Hari":
      return "bg-blue-100 text-blue-700";
    default:
      return "bg-gray-100 text-gray-700";
  }
};

const DueThisWeekTable = ({ data }: { data: DueThisWeek[] }) => {
  return (
    <Card className="bg-white rounded-lg shadow-sm">
      <CardHeader className="pb-2">
        <CardTitle className="text-lg font-semibold">Jatuh Tempo Minggu Ini</CardTitle>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow className="hover:bg-transparent">
              <TableHead className="w-[120px]">No. Pinjaman</TableHead>
              <TableHead>Nama Anggota</TableHead>
              <TableHead>Jenis</TableHead>
              <TableHead>Jumlah</TableHead>
              <TableHead>Tanggal</TableHead>
              <TableHead>Status</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {data.map((item) => (
              <TableRow key={item.noPinjaman} className="hover:bg-gray-50">
                <TableCell className="font-medium">{item.noPinjaman}</TableCell>
                <TableCell>{item.namaAnggota}</TableCell>
                <TableCell>{item.jenis}</TableCell>
                <TableCell>{item.jumlah}</TableCell>
                <TableCell>{item.tanggal}</TableCell>
                <TableCell>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusStyle(item.status)}`}>
                    {item.status}
                  </span>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
};

export default DueThisWeekTable;
