"use client";

import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface LatestTransaction {
  noTransaksi: string;
  tanggal: string;
  anggota: string;
  jenis: string;
  jumlah: string;
  status: string;
}

const getStatusStyle = (status: string) => {
  switch (status) {
    case "Lunas":
      return "bg-green-100 text-green-700";
    case "Selesai":
      return "bg-blue-100 text-blue-700";
    default:
      return "bg-gray-100 text-gray-700";
  }
};

const LatestTransactionsTable = ({ data }: { data: LatestTransaction[] }) => {
  return (
    <Card className="bg-white rounded-lg shadow-sm">
      <CardHeader className="pb-2">
        <CardTitle className="text-lg font-semibold">Transaksi Terbaru</CardTitle>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow className="hover:bg-transparent">
              <TableHead className="w-[120px]">No. Transaksi</TableHead>
              <TableHead>Tanggal</TableHead>
              <TableHead>Anggota</TableHead>
              <TableHead>Jenis</TableHead>
              <TableHead>Jumlah</TableHead>
              <TableHead>Status</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {data.map((item) => (
              <TableRow key={item.noTransaksi} className="hover:bg-gray-50">
                <TableCell className="font-medium">{item.noTransaksi}</TableCell>
                <TableCell>{item.tanggal}</TableCell>
                <TableCell>{item.anggota}</TableCell>
                <TableCell>{item.jenis}</TableCell>
                <TableCell>{item.jumlah}</TableCell>
                <TableCell>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusStyle(item.status)}`}>
                    {item.status}
                  </span>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
};

export default LatestTransactionsTable;
